@import '#main-styles/colorVariables';

.wrapper{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.wrapper:hover{
  box-shadow: inset 0 0 .4rem 1px $hoverSell;
}

.active{
  transition: .7s;
  animation: flashing 1s linear infinite alternate 0s forwards;
}

@keyframes flashing{
  0%{
    background-color: $bgcolor;
  }
  100%{
    background-color: $flishingSell;
  }
}




