@import '#main-styles/colorVariables';

.wrapper{
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60%;
  height: 60%;
  transform: translateX(-50%) translateY(-50%);

  fill: $zerroFigure;
  transition: fill;

  stroke: $zerroFigure;
  stroke-width: 4px;
  
}
.activeWrapp{
  animation: moving 3s linear infinite alternate 0s forwards;
}
  


@keyframes moving{
   0%{top: 45%}
  25%{left: 53%}
  50%{top: 54%}
  75%{left: 45%}
}