@import '#main-styles/variables.scss';

.wrapper{
  color: $active-buttons;
  position: relative;
  // display: grid;
  
  // grid-template-columns: repeat(2, auto);
  // grid-template-rows: repeat(2, auto);
  // grid-column-gap: 1rem;
  // grid-row-gap: 0px;
}

.window{
  position: relative;
  width: 3rem;
  height: 3rem;
  border-radius: .5rem;
  cursor: pointer;
  overflow: hidden;
  background-color: rgba(26, 26, 26, 0.87);
  display: flex;
  justify-content: center;
}


.radio{
  position: absolute;
  right: -1.5rem;
  top: 0;
}

.button{
  transform: translateX(-2rem);
}


.marker{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);

  display: block;
  width: 1rem;
  height: 1rem;
  background-color: rgb(156, 235, 255);
  border-radius: .25rem;
  box-shadow: 0 0 .5rem .35rem rgb(255, 255, 255);
  animation: moving 1s linear infinite alternate 0s forwards;
  
}


@keyframes moving{
  0%{
    top: 59%;
  }
  25%{
    left: 61%;
  }
  50%{
    top:41%
  }
  75%{
    left: 41%;
  }
}